/* eslint-disable */
import NavigationBar from '@/components/NavigationBar';
import { onMounted, ref } from 'vue';
import Frame from '@/common/frame';
export default {
  name: 'PersonalInfoController',
  components: {
    NavigationBar
  },

  setup() {
    const grid = ref(null);
    const items = [{
      title: '账号',
      value: '皖A23132131'
    }, {
      title: '姓名',
      value: '李大富'
    }, {
      title: '手机号',
      value: '189 2668 8080'
    }, {
      title: '岗位',
      value: '大区经理'
    }];
    const areas = ['珠海市', '珠海市珠海市', '广东省/珠海市/香洲区', '广东省市/斗门区123', '广东/金湾区123123', '广东省/珠海市/金湾区'];
    onMounted(() => {
      const maxWitdh = grid.value.offsetWidth;
      const children = [...grid.value.children];
      let tempFrame = null;
      let frames = children.map(item => new Frame(item));

      for (let i = 0; i < frames.length; i++) {
        const frame = frames[i];

        if (tempFrame) {
          const w = tempFrame.x + tempFrame.width + 16 + frame.width + 16;

          if (w < maxWitdh) {
            frame.x = tempFrame.x + tempFrame.width + 16;
            frame.y = tempFrame.y;
          } else {
            frame.x = 16;
            frame.y = tempFrame.y + tempFrame.height + 16;
          }
        } else {
          frame.y = 0;
          frame.x = 16;
        }

        tempFrame = frame;
        frame.updateFrame();
      }

      if (frames.length > 0) {
        frames.lastIndexOf();
        grid.value.style.height = frames[frames.length - 1].y + frames[frames.length - 1].height + 16 + 'px';
      }
    });
    return {
      items,
      areas,
      grid
    };
  }

};