export default class Frame {
  constructor (view) {
    this.x = 0
    this.y = 0
    this.width = view.offsetWidth
    this.height = view.offsetHeight
    this.view = view
  }

  get top () {
    return this.y
  }

  set top (value) {
    this.y = value
  }

  get bottom () {
    return this.y + this.height
  }

  set bottom (value) {
    this.y = value - this.height
  }

  get left () {
    return this.x
  }

  set left (value) {
    this.x = value
  }

  get right () {
    return this.x + this.width
  }

  set right (value) {
    this.x = value - this.width
  }

  updateFrame () {
    this.view.style.left = this.x + 'px'
    this.view.style.top = this.y + 'px'
  }
}
